div.item {
	/*width: 20vw;*/
    /*height: 25vh;*/
    min-height: 75px;
    max-height: 230px;
    height:15vh;
    -ms-flex: 1 0 auto;
        flex: 1 0 auto;
    min-width: 15vw;
    max-width: 300px;
    margin: 15px 10px;
    padding-right: 15px;
    padding-left: 15px;
    background-position: center;
    background-size: cover;
    cursor: pointer;
    display:-ms-inline-flexbox;
    display:inline-flex;
  	-ms-flex-pack: center;
  	    justify-content: center;
  	-ms-flex-align: center;
  	    align-items: center;
}

div.item .textfit {
	color: white;
	width:100%;
	-ms-flex: none;
	    flex: none;
	/*position: relative;*/
	white-space: normal;
	/*top:30%;*/
	text-shadow: black 0 0 0.1em, black 0 0 0.1em, black 0 0 0.1em, black 0 0 0.1em, black 0 0 0.2em;
	/*font-size: calc(16px + 1.45vw);
*/
	-webkit-user-select: none;  
  	-moz-user-select: none;     
  	-ms-user-select: none;      
  	user-select: none;          
}

div.item.on {
	-webkit-box-shadow: 0px 0px 0px 6px #333333;
	        box-shadow: 0px 0px 0px 6px #333333;
	-webkit-transition-property: -webkit-box-shadow;
	transition-property: -webkit-box-shadow;
	-o-transition-property: box-shadow;
	transition-property: box-shadow;
	transition-property: box-shadow, -webkit-box-shadow;
	-webkit-transition-duration: 0.25s;
	     -o-transition-duration: 0.25s;
	        transition-duration: 0.25s;
	-webkit-transition-timing-function: linear;
	     -o-transition-timing-function: linear;
	        transition-timing-function: linear;
}

div.item.off {
	-webkit-box-shadow: 0px 0px 0px 0px #333333;
	        box-shadow: 0px 0px 0px 0px #333333;
	-webkit-transition-property: -webkit-box-shadow;
	transition-property: -webkit-box-shadow;
	-o-transition-property: box-shadow;
	transition-property: box-shadow;
	transition-property: box-shadow, -webkit-box-shadow;
	-webkit-transition-duration: 0.25s;
	     -o-transition-duration: 0.25s;
	        transition-duration: 0.25s;
	-webkit-transition-timing-function: linear;
	     -o-transition-timing-function: linear;
	        transition-timing-function: linear;
}

